.button {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    border: none;
    height: 36px;
    padding: 0 24px;
    border-radius: 20px;
    font-weight: 500;
    transition: background-color var(--md-sys-motion-duration-150) cubic-bezier(0.2, 0, 0, 1) 0ms,
        box-shadow var(--md-sys-motion-duration-150) cubic-bezier(0.2, 0, 0, 1) 0ms,
        border-color var(--md-sys-motion-duration-150) cubic-bezier(0.2, 0, 0, 1) 0ms,
        color var(--md-sys-motion-duration-150) cubic-bezier(0.2, 0, 0, 1) 0ms;

    &:disabled {
        pointer-events: none;
        cursor: default;
    }

    span {
        font-weight: var(--md-sys-typescale-label-large-weight);
        font-size: 18px;
        font-style: var(--md-sys-typescale-label-large-font-style);
        letter-spacing: var(--md-sys-typescale-label-large-tracking);
        line-height: var(--md-sys-typescale-label-large-line-height);
        text-transform: var(--md-sys-typescale-label-large-text-transform);
        text-decoration: var(--md-sys-typescale-label-large-text-decoration);
    }

    &.isActive {
        filter: brightness(0.75);
    }
}

.iconButton,
.iconButtonWithText {
    width: 40px;
    min-width: unset !important;
    height: 40px;
    padding: unset;
    border-radius: 24px;

    span {
        font-size: 24px;
    }
}

.iconButtonWithText {
    width: unset;
    padding: 0 16px !important;
}
